body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

html {
  height: 100%;
  background: -webkit-gradient(linear,left top, left bottom,from(#654a86),to(#534292));
  background: -webkit-linear-gradient(top,#654a86,#534292);
  background: -o-linear-gradient(top,#654a86,#534292);
  background: linear-gradient(to bottom,#654a86,#534292);
}

